html{font-size:14px;font-family:'Roboto';color:#26d3ff;text-shadow:0 0 1px #fff;text-align:left}table{border-collapse:collapse;width:100%}table,th,td{border-left:1px solid #000;border-bottom:1px solid #000}table th{border-bottom:3px solid #2684a6}table td{border-left:3px solid #2684a6}th{color:#ffd326}tr:hover{color:#d3ff26;text-shadow:0 0 2px #e8ff3a;background-color:rgba(58,189,255,0.3)}td:hover{color:#ff5926;text-shadow:0 0 2px #ff6d3a;background-color:rgba(58,189,255,0.4)}body{background-color:#000}



